.list-bullet {
	> li {
		position: relative;
		padding-left: 30px;

		&:nth-child(1n+2) {
			margin-top: 16px; }

		&:before {
			@include b;
			left: 0;
			top: 8px;
			width: 10px;
			height: 10px;
			border-radius: 50%;
			background: $color-green; } } }

.list-order {
	display: table;
	counter-reset: foo;
	> li {
		//display: table-row
		display: flex;
		counter-increment: foo;
		&:nth-child(1n+2) {
			margin-top: 16px; }

		&:before {
			display: table-cell;
			content: "0"counter(foo);
			text-align: left;
			padding-right: 12px; } } }

