.p-competition {}

.list-video {
	&__item {
		&:nth-child(1n+2) {
			@extend .offset-top_md; } } }

.card-media {
	@include make-row();
	--bs-gutter-y: 30px;
	&__video-w {
		@include make-col-ready();
		@include min-screen($mid-tablet) {
			@include make-col(6); }
		@include min-screen($desktop) {
			@include make-col(5); }
		iframe, video {} }
	&__content {
		@include make-col-ready();
		@include min-screen($mid-tablet) {
			@include make-col(6); }
		@include min-screen($desktop) {
			@include make-col(5); } }
	&__title {
		@extend h3; }
	&__autor {
		margin-top: 20px; }
	&__more {
		margin-top: 20px; } }
