// Variables:
//   $units: 1px;                     // default units for 'screen' mixins - it must include a digit!
//
// Mixins:
//   @ min-screen(width)              // shortcut for @media screen and (min-width ...)
//   @ max-screen(width)              // shortcut for @media screen and (max-width ...)
//   @ screen(min-width, max-width)   // shortcut for @media screen and (min-width ...) and (max-width ...)
//   ---
//   @ hdpi(ratio)                    // devices with hidpi displays (default ratio: 1.3)
//
// ---------------------------------------------------------------------------------------------------------------------

$units: 1px !default;

@mixin screen($res-min, $res-max, $include-min: false) {
	@media screen and (min-width: if(($include-min), $res-min, $res-min + 1)) and (max-width: if(unitless($res-max), $res-max*$units, $res-max)) {
		@content;
	}
}

@mixin max-screen($res, $include-resolution: false) {
	@media screen and (max-width: if(($include-resolution), $res - 1, $res)) {
		@content;
	}
}

@mixin min-screen($res, $include-resolution: false) {
	@media screen and (min-width: if(($include-resolution), $res, $res + 1)) {
		@content;
	}
}

@mixin screen-maw-mah($maw, $mah) {
	@media screen and (max-width: $maw), (max-height: $mah) {
		@content;
	}
}

@mixin screen-miw-mih($miw, $mih) {
	@media screen and (min-width: $miw) and (min-height: $mih) {
		@content;
	}
}

@mixin screen-mah($mah) {
	@media screen and (max-height: $mah) {
		@content;
	}
}

@mixin screen-mih($mih) {
	@media screen and (min-height: $mih) {
		@content;
	}
}