.footer {
	background: $color-black;
	color: $color-white;
	font-size: 16px;
	line-height: 1.375;
	&__sponsors {
		@extend .space-v;
		@include max-screen($ipad-landscape) {
			padding-top: 50px;
			padding-bottom: 50px; } } }


.grid-sponsors {
	display: grid;
	grid-template-columns: repeat(3, minmax(0, 1fr));
	grid-gap: 40px;
	@include min-screen($smartphone-landscape) {
		grid-template-columns: repeat(4, minmax(0, 1fr)); }
	@include min-screen($mid-tablet) {
		grid-template-columns: repeat(8, minmax(0, 1fr)); }
	@include min-screen($desktop) {
		grid-gap: 40px; } }

.footer-ticker {
	width: 100%;
	height: 68px;
	//background-image: url("../img/tickers/buy-tickets-RU.png")
	background-repeat: repeat;
	animation: tickerToLeft 4s infinite linear; }

@keyframes tickerToLeft {
	from {
		background-position: 0 50%; }
	to {
		background-position: calc(var(--tickerWidth) * -1) 0; } }

@keyframes tickerToLeftMobile {
	from {
		background-position: 0 50%; }
	to {
		background-position: -200vw 50%; } }

@keyframes tickerToRight {
	from {
		background-position: 0 50%; }
	to {
		background-position: calc(var(--tickerWidth)) 0; } }

@keyframes tickerToRightMobile {
	from {
		background-position: 0 50%; }
	to {
		background-position: 200vw 50%; } }


.footer-top {
	@extend .space-v;
	@include max-screen($desktop) {
		padding-top: 50px;
		padding-bottom: 50px; }
	@include max-screen($ipad-landscape, true) {
		display: none; }
	&__inner {} }


.footer-bot {
	//padding: 18px 0
	padding-bottom: 18px;
	@include max-screen($ipad-landscape, true) {
		padding-top: 18px; }
	&__inner {
		display: flex;
		justify-content: space-between; } }

.ft-grid {
	@include make-row();
	&__col {
		@include make-col-ready();
		&--logo {
			@include make-col(2);
			@include min-screen($desktop) {
				@include make-col(4); }
			img {
				max-width: 197px; } }
		&--nav {
			@include make-col(10);
			@include min-screen($desktop) {
				@include make-col(8); } } } }

.fnav-grid {
	@include make-row();
	&__col {
		@include make-col-ready();
		@include make-col(1, 4); } }

.fnav-list {
	$this: &;
	&--lg {
		#{$this}__item {
			margin-bottom: 38px; }
		#{$this}__link {
			font-family: $ffamily-bold; } }

	&__item {
		&:first-child {
			margin-bottom: 38px;
			#{$this}__link {
				font-family: $ffamily-bold; } }
		&:nth-child(1n+2) {
			margin-top: 15px; } }

	&__link {
		@extend .link;
		display: inline-block; } }
