// core
.swiper {
	$this: &; }

// modifications
.swiper--default {
	picture {
		min-height: 200px; }
	img {
		min-height: 200px;
		object-fit: cover; } }

.swiper--cards {
	.swiper-wrapper,
	.swiper-slide {
		height: auto; }
	.swiper-slide {
		display: flex;
		> * {
			width: 100%; } } }

.swiper--slideshow {
	width: 100%;
	transform: translateZ(0);
	backface-visibility: hidden;
	transform-origin: 50% 50%;
	overflow: hidden;
	.swiper-slide {
		padding: 1px 4px;
		opacity: 0;
		visibility: hidden;
		background: $color-white;
		&.swiper-slide-active {
			visibility: visible;
			opacity: 1;
			z-index: 2; }
		img {
			width: 100%; } } }

.swiper-panel {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 30px;
	&__pagination-w {}
	&__pagination {
		@extend h4; }
	&__controls {} }

.swiper-controls {
	$this: &;
	display: flex;
	&--size_small {
		#{$this}__btn {
			font-size: 22px;
			&--next {
				margin-left: 10px; } } }

	&__btn {
		font-size: 22px;
		transition: all .2s;
		@include min-screen($small-tablet) {
			font-size: 33px; }
		&:hover {
			color: $color-main; }

		&.swiper-button-disabled {
			color: $color-text;
			opacity: .4;
			cursor: default; }
		&--prev {
			@include icon(arrow-left); }
		&--next {
			@include icon(arrow-right);
			margin-left: 20px;
			@include min-screen($small-tablet) {
				margin-left: 40px; } } } }
