// если цветов много - http://chir.ag/projects/name-that-color/#6195ED
// есть модуль для генерации название цветов в проекте ntc-generator.js

// Default
$color-black: #000000;
$color-white: #ffffff;

$color-yellow: #ffe600;
$color-purple: #8162ec;
$color-red: #f40303;
$color-green: #54b295;
$color-gray: #c4c4c4;

$color-border: #7b7b7b;

// Specific
$color-main: $color-purple;
$color-main--light:  lighten($color-main, 5%);
$color-main--dark: darken($color-main, 20%);

// Typography
$color-text: $color-black;
$color-text-gray: #888888;


// массивы для генерации классов с цветами
$bg-map: (
	black: $color-black,
);
$colors-map: (
	white: $color-white,
);

@include create-background-classes($bg-map);
@include create-colors-classes($colors-map);
