@mixin buttonCore($minHeight, $fontSize, $lineHeight, $verticalPadding: 20px) {
	$contentHeight: floor($fontSize * $lineHeight);
	$buttonSingleSpace: ($minHeight - $contentHeight) / 2;
	display: inline-block;
	font-size: $fontSize;
	line-height: $lineHeight;
	padding: ($buttonSingleSpace + 3px) $verticalPadding ($buttonSingleSpace - 3px); }

.btn {
	@include radial-out();
	font-family: $ffamily-bold;
	background: transparent;
	border-radius: 50%;
	text-align: center;
	border: 2px solid var(--btn-bg);
	transition: all .3s ease-out; }

.btn--theme_white {
	--btn-bg: #{$color-white};
	--btn-color: #{$color-black}; }
.btn--theme_black {
	--btn-bg: #{$color-black};
	--btn-color: #{$color-white}; }
.btn--theme_transparent {
	border: 2px solid currentColor; }

.btn--size_md {
	@include buttonCore(76px, 18px, 1.3, 28px);
	@include max-screen($common-tablet) {
		font-size: 16px;
		padding-left: 20px;
		padding-right: 20px; } }

.btn--size_sm {
	@include buttonCore(52px, 14px, 1.3, 15px);
	@include max-screen($smartphone-landscape) {
		font-size: 12px;
		padding: 17px 10px 11px; }
	@include max-screen(420px) {
		font-size: 10px; } }


