.btn-status {
	$this: &;
	position: relative;
	&--ajax {
		background: transparent;
		user-select: none;
		pointer-events: none;
		cursor: default;
		#{$this}__text {
			display: none;
			opacity: 0; }
		#{$this}__spinner {
			display: block;
			opacity: 1; } }

	&__text {
		display: block;
		transition: opacity .2s; }
	&__spinner {
		@include loader02(40px, $color-main, 6px);
		display: none;
		transition: opacity .2s;
		margin: 0 auto; } }
