 :root {
	--header_height: 86px;
	@include max-screen($smartphone-landscape) {
		--header_height: 74px; } }

.container--space_left {
	@include min-screen($desktop) {
		padding-left: 100px; } }

.container--space_right {
	@include min-screen($desktop) {
		padding-right: 100px; } }

.container--space_x {
	@include min-screen($desktop) {
		padding-left: 100px;
		padding-right: 100px; } }

.wrapper {
	$this: &;
	padding-top: var(--header_height); // header height

	position: relative;
	display: flex;
	flex-direction: column;
	overflow: hidden; // test
	min-height: 100vh; // sticky footer
	&--home {
		padding-top: 0; }
	&--notfound {
		background: $color-yellow;
		#{$this}__inner {
			padding-bottom: 0 !important; } }
	&__inner {
		flex: 1;
		@extend .space-bottom; } }

.page-head {
	position: relative;
	@include max-screen($big-tablet) {
		margin-bottom: -40px; }

	&__figure {
		position: relative; }
	&__img-w {
		img {
			width: 100%;
			min-height: 200px;
			object-fit: cover; } }
	&__content {
		@include centre(both);
		width: 100%; }
	&__title {
		max-width: 700px;
		color: $color-white; } }

section {
	@extend .offset-top; }

.grid-1-3 {
	display: grid;
	grid-template-columns: repeat(2, minmax(0, 1fr));
	grid-gap: 40px 60px;
	@include max-screen($desktop) {
		padding-left: 20px;
		padding-right: 40px; }
	@include max-screen($small-tablet) {
		padding-left: 0;
		padding-right: 20px;
		grid-gap: 40px; }
	@include max-screen($smartphone-portrait) {
		padding-right: 10px;
		grid-gap: 40px 30px; }

	@include min-screen($small-tablet) {
		grid-template-columns: repeat(3, minmax(0, 1fr)); } }

.section-head {
	$this: &;
	@extend .container--space_left;
	display: flex;
	justify-content: space-between;
	margin-bottom: 40px;
	&--title_small {
		margin-bottom: 30px;
		#{$this}__title {
			@extend h4;
			@include max-screen($big-tablet) {
				font-size: 20px !important; } } }

	&__title {}
	&__extra {
		margin-left: 10px; } }

.section-title {
	@extend .offset-bottom_md; }
