.static-content {
	> *:first-child {
		margin-top: 0 !important; }

	> *:last-child {
		margin-bottom: 0 !important; }

	h3 {
		@extend h3;
		@extend .offset-top_md;
		font-family: $ffamily-bold; }

	h5 {
		font-size: inherit;
		font-weight: 700;
		margin-bottom: -14px;
		margin-top: 30px; }

	strong, b {
		font-weight: 700; }

	img {
		display: block;
		margin: 40px 0; }

	ul {
		@extend .list-bullet; }

	ol {
		@extend .list-order; }

	p {
		margin: 22px 0; }

	a {
		@extend .link-main; } }
