/*
		|--------------------------------------------------------------------------
		| UTILS
		|-------------------------------------------------------------------------- */

@mixin device-touch {
	@media (hover: none) and (pointer: coarse) {
		@content; } }

@mixin device-desktop {
	@media (pointer: fine) {
		@content; } }

// use with 'object-fit' polyfill
@mixin object-fit($fit: fill, $position: null) {
	object-fit: $fit;
	@if $position {
		object-position: $position;
		font-family: 'object-fit: #{$fit}; object-position: #{$position}'; }
	@else {
		font-family: 'object-fit: #{$fit}'; } }

@mixin centre($axis: "both") {
	@if $axis == "y" {
		position: absolute;
		top: 50%;
		transform: translateY(-50%); }
	@if $axis == "x" {
		position: absolute;
		left: 50%;
		transform: translateX(-50%); }
	@if $axis == "both" {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%); }
	@if $axis == "none" {
		position: relative;
		top: auto;
		left: auto;
		transform: none; } }

@mixin shadow-border($width, $color) {
	box-shadow: inset 0 0 0 $width $color; }

@mixin bg-cover {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%; }

@mixin ghostVerticalAlign {
	&:before {
		content: '';
		display: inline-block;
		vertical-align: middle;
		height: 100%;
		width: .1px; } }

@mixin b {
	display: block;
	position: absolute;
	content: " "; }

/*
		|--------------------------------------------------------------------------
		| Text stuff
		|-------------------------------------------------------------------------- */

@mixin text-overflow {
	overflow: hidden;
	-ms-text-overflow: ellipsis;
	text-overflow: ellipsis;
	white-space: nowrap; }

@mixin text-hide {
	font: 0/0;
	text-shadow: none;
	color: transparent; }

@mixin text-prevent {
	-ms-word-break: break-all;
	word-break: break-all;
	word-break: break-word;
	hyphens: auto; }

/*
		|--------------------------------------------------------------------------
		| Font
		|-------------------------------------------------------------------------- */

@mixin font($alias, $name) {
	@font-face {
		font-family: $alias;
		src: url("../fonts/" + $name + ".eot");
		src: url("../fonts/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + ".woff") format("woff"), url("../fonts/" + $name + ".ttf") format("truetype");
		font-weight: normal;
		font-style: normal; } }

@mixin font-smoothing($val: antialiased) {
	-webkit-font-smoothing: $val; }

/*
		|--------------------------------------------------------------------------
		| Placeholder
		|-------------------------------------------------------------------------- */
@mixin placeholder {
	&:-ms-input-placeholder {
		@content; }
	&:-moz-placeholder {
		@content; }
	&::-moz-placeholder {
		@content; }
	&::-webkit-input-placeholder {
		@content; }
	&::placeholder {
		@content; } }

/*
		|--------------------------------------------------------------------------
		| Scrollbar
		|-------------------------------------------------------------------------- */
@mixin scrollbar {
	&::scrollbar {
		@content; } }

@mixin scrollbar-track {
	&::scrollbar-track {
		@content; } }

@mixin scrollbar-thumb {
	&::scrollbar-thumb {
		@content; } }

@mixin scrollbar-track-piece {
	&::scrollbar-track-piece {
		@content; } }
