.swal-footer {
	text-align: center; }

.swal-button {
	@include buttonCore(46px, 16px, 1.2);
	transition: background .2s;
	&--success {
		background-color: $color-main;
		&:hover, &:focus {
			background-color: $color-main--light!important; } }
	&--error {
		background-color: #f27474;
		&:hover, &:focus {
			background-color: lighten(#f27474, 5%)!important; } } }


