.p-home {}

.hero {
	--circle-radius: 18.22vmax;
	position: relative;
	user-select: none;
	height: calc(100vh - var(--header_height));
	min-height: 568px;
	margin-top: var(--header_height);
	overflow: hidden;
	&--device_touch {
		//cursor: none
		height: calc(var(--vh, 1vh) * 100 - var(--header_height)); }
	&__layer {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&--upper {
			z-index: 1;
			color: $color-white;
			background: $color-black; }
		&--lower {
			z-index: 2;
			background-color: $color-yellow;
			background-image: url("../img/hero-bg.png");
			background-repeat: no-repeat;
			background-position: 40% 40%;
			background-size: cover;
			color: $color-black;
			clip-path: circle(var(--circle-radius) at var(--clip-x) var(--clip-y));
			will-change: clip-path;
			transition: clip-path .4s $ease-out-cubic; } } }

.hero-upper {
	&__text {
		@extend .container;
		position: absolute;
		bottom: 74px;
		left: 50%;
		transform: translateX(-50%);
		width: 100%;
		//font-size: 144px
		font-size: clamp(40px, 7.5vw, 144px);
		font-family: $ffamily-bold;
		letter-spacing: 2.88px;
		line-height: 1.041; } }

.hero-lower {
	padding-top: 46px;
	&__logo {
		max-width: 1040px;
		margin: 0 auto;
		padding: 0 20px; }
	&__content {
		//margin: 200px auto 0
		margin: 20vh auto 0;
		max-width: 845px;
		padding: 0 20px;
		font-family: $ffamily-bold; }
	&__text {
		//font-size: 54px
		font-size: clamp(18px, 2.8vw , 54px);
		line-height: 1.11;
		br {
			@include max-screen($smartphone-landscape) {
				display: none; } } }
	&__date {
		//margin-top: 0
		margin-top: 30px; }
	&__more {
		margin-top: 40px; } }

.presentation {
	@include make-row();
	--bs-gutter-x: 80px;
	@include max-screen($small-tablet) {
		--bs-gutter-x: 40px; }
	@include max-screen($smartphone-wide) {
		--bs-gutter-x: 20px; }

	&__col {
		@include make-col-ready();
		&--logo {
			width: 120px;
			@include max-screen($smartphone-wide) {
				width: 80px; }
			@include min-screen($small-tablet) {
				@include make-col-offset(1);
				@include make-col(3); } }
		&--content {
			flex: 1;
			@include min-screen($small-tablet) {
				@include make-col(8); }
			@include min-screen($big-tablet) {
				@include make-col(6); } } }

	&__text {
		@extend h4; } }

.card-review {
	display: flex;
	flex-direction: column;
	background: $color-yellow;
	padding: 20px;
	@include min-screen($big-tablet) {
		padding: 20px 40px; }
	@include min-screen($notebook-width) {
		padding: 30px 80px; }

	&__text {
		@extend h5;
		max-width: 314px;
		margin-bottom: 32px; }
	&__written-by {
		margin-top: auto; } }


.parallax-section {
	position: relative;
	perspective: 140px;
	perspective-origin: 50% 50%;
	overflow: hidden;
	&__pillow {
		img {
			width: 100%;
			min-height: 460px; } } // na shary
	&__window {
		@include centre(both);
		max-width: 1400px;
		width: 100%;
		border-radius: 100%;
		overflow: hidden;
		@include max-screen($ipad-landscape) {
			max-width: calc(100% - 40px); }
		@include max-screen($mid-tablet) {
			max-width: 100%; }
		img {
			transform: scale(1.2); } }
	&__content {
		@extend .container;
		@extend .container--space_left;
		@include centre(x);
		//top: 200px
		top: 120px;
		color: $color-white;
		@include max-screen($mid-tablet) {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			top: 40px;
			bottom: 40px; } }

	&__title {
		@extend h1;
		@include max-screen($mid-tablet) {
			margin-bottom: 30px; } }
	&__more {
		margin-top: 54px;
		@include max-screen($mid-tablet) {
			margin-top: auto; } } }



.reasons {
	$this: &;
	&--arrows {
		#{$this}__text {
			@include icon(arrow-right, after);
			&:after {
				font-size: 22px;
				margin-left: 14px; } } }

	&--text_large {
		#{$this}__item {
			@include screen($smartphone-portrait, $small-tablet) {
				padding-top: 40px;
				padding-bottom: 33px; } }

		#{$this}__desc {
			@include max-screen($desktop) {
				align-items: baseline; } }
		#{$this}__text {
			@extend h2;
			max-width: 100%; }
		#{$this}__number {
			@include max-screen($small-tablet) {
				margin-right: 20px; } } }


	&__item {
		display: block;
		position: relative;
		padding: 27px 0 20px;
		border-top: 2px solid $color-purple;
		@include min-screen($small-tablet) {
			padding: 47px 0 40px; }
		&:last-child {
			border-bottom: 2px solid $color-purple; } }
	&__desc {
		@extend .container--space_left;
		display: flex;
		align-items: flex-start; }

	&__number {
		@extend h2;
		min-width: 35px;
		margin-right: 60px;
		@include max-screen($desktop) {
			margin-right: 34px;
			padding-top: 6px; }
		@include max-screen($smartphone-landscape) {
			margin-right: 20px; } }

	&__text {
		flex: 1;
		max-width: 964px;
		display: flex;
		justify-content: space-between;
		align-items: center; }

	&__hover-wrap {
		position: absolute;
		left: 0;
		top: 0;
		display: flex;
		width: 100%;
		height: 100%;
		z-index: 1;
		@include max-screen($ipad-landscape) {
			display: none; } }

	&__hover-part {
		position: relative;
		//display: none // ?
		flex: 1;
		height: 100%;
		cursor: none;
		//height: 20px
		//border: 1px dashed greenyellow
		//&:hover
		//mg
		//opacity: 1!important
		img {
			opacity: 0;
			visibility: hidden;
			position: absolute;
			//position: fixed
			top: 0;
			left: 0;
			z-index: -1;
 } }			//transition: opacity .07s linear

	&__more {
		@extend .offset-top_md;
		max-width: 208px;
		width: 100%; } }

.card-life {
	$this: &;
	&:hover, &:focus {
		#{$this}__figure {
			//transform: translate(26px, 26px)
			transform: translate(7.55%, 5.18%); }
		#{$this}__img {
			transform: translate(0px, 0px); } }
	&__figure-w {
		@extend .offset-bottom_sm;
		position: relative; }

	&__figure {
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
		//transition: transform 1.3s $ease-in-circ
		transition: transform .6s ease-in-out; }
	&__img {
		//transform: translate(26px, 26px)
		transform: translate(7.55%, 5.18%);
		transition: transform .6s ease-in-out; }
	&__text {
		@extend h4;
		@include max-screen($common-tablet) {
			font-size: 18px; }
		@include max-screen($smartphone-landscape) {
			font-size: 16px; }
		@include min-screen($desktop) {
			padding-left: 26px; } } }

.banner {
	$this: &;
	position: relative;
	&--content_center {
		#{$this}__content {
			@include max-screen($notebook-width) {
				padding-top: 220px;
				padding-bottom: 120px; }
			@include min-screen($notebook-width) {
				top: 50%;
				transform: translate(-50%, -50%); } } }
	&__img-w {
		@include max-screen($notebook-width) {
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
			width: 100%;
			height: 100%; }
		img {
			width: 100%;
			object-fit: cover;
			@include max-screen($notebook-width) {
				height: 100%; }
			@include min-screen($notebook-width) {
				min-height: 540px; } } }
	&__tickers {
		position: absolute;
		top: 120px;
		left: 0;
		width: 100%;
		z-index: 1;
		@include max-screen(1800px) {
			top: 20px; }
		@include max-screen($notebook-width) {
			top: 0; } }

	&__ticker {
		width: 100%;
		height: 80px;
		background-repeat-y: no-repeat;
		@include max-screen($small-tablet) {
			height: 42px;
			background-size: 200%; }
		@include max-screen($notebook-width) {
			background-color: $color-black; }
		&--top {
			animation: tickerToRight 14s infinite linear;
			@include max-screen($small-tablet) {
				animation: tickerToRightMobile 10s infinite linear; } }
		&--bottom {
			animation: tickerToLeft 14s infinite linear;
			@include max-screen($small-tablet) {
				animation: tickerToLeftMobile 10s infinite linear; } } }
	&__content {
		color: $color-white;
		@include max-screen($notebook-width) {
			padding-top: 60px;
			padding-bottom: 60px; }
		@include min-screen($notebook-width) {
			position: absolute;
			top: 112px;
			left: 50%;
			transform: translateX(-50%); } }
	&__title {
		@extend h1;
		max-width: 740px;
		margin-bottom: 32px; }
	&__text {
		@extend h5;
		max-width: 600px; }
	&__more {
		margin-top: 56px; } }

.offer {
	@include max-screen($desktop) {
		@include make-row();
		--bs-gutter-y: 40px; }
	@include max-screen($common-tablet, true) {
		padding-top: 60px; }
	@include min-screen($desktop) {
		display: flex; }
	&__col {
		@include max-screen($desktop) {
			@include make-col-ready(); }

		&--info {
			@include max-screen($desktop) {
				@include make-col(8); }
			@include max-screen($common-tablet, true) {
				@include make-col(12);
				order: 1; }
			@include min-screen($desktop) {
				flex: 1;
				padding-right: 40px; } }
		&--slideshow {
			@include max-screen($desktop) {
				@include make-col(4); }
			@include max-screen($common-tablet, true) {
				@include make-col(6);
				min-width: 300px;
				margin: 0 auto; }
			@include max-screen($smartphone-portrait, true) {
				min-width: 260px; }
			@include min-screen($desktop) {
				width: 510px; } } }

	&__content {
		max-width: 720px; }
	&__title {
		@extend h1; }
	&__text {
		@extend h5;
		max-width: 580px;
		margin-top: 28px; }
	&__more {
		@extend .offset-top_md; } }

.slideshow {
	&__item {
		display: none;
		&--active {
			display: block; } }
	&__img {} }
