* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	&:before, &:after {
		box-sizing: inherit; } }

html {
	-webkit-text-size-adjust: 100%;
	// scrollreveal
	//&.sr
	//section-head > *,
	//data-reveal-block],
	//data-reveal-grid] > *,
	//data-reveal-list] > *
 }	//visibility: hidden

body {
	min-width: 320px;
	font-size: 20px;
	line-height: 1.4;
	font-family: $ffamily-reg;
	//letter-spacing: 0.64px
	color: $color-text;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	@include max-screen($big-tablet) {
		font-size: 18px; }
	@include max-screen($smartphone-portrait) {
		font-size: 16px; } }


svg, picture {
	display: block; }

img {
	max-width: 100%;
	height: auto;
	border: 0;
	vertical-align: middle; }

ul, li {
	list-style: none; }

a, input, textarea {
	color: inherit;
	font-family: inherit; }

a {
	font-size: inherit;
	text-decoration: none; }

textarea {
	resize: none; }

table {
	border-collapse: collapse;
	border-spacing: 0; }

td, th {
	padding: 0; }

button {
	margin: 0;
	background: transparent;
	outline: none;
	border: none;
	cursor: pointer;
	overflow: visible; }

input {
	margin: 0;
	outline: 0; }

//
//button><i class="icon-close"></i></button> ok
//button class="icon-close"></button> ne ok
i {
	display: inline-block;
	font: normal normal normal 16px/1 "gulp-svg-font";
	vertical-align: middle;
	&.icon-flip {
		transform: scaleX(-1); }
	&.icon-flip-y {
		transform: scaleY(-1); }
	//&.icon-lg
	//ont-size: 32px
	//&.icon-sm
 }	//ont-size: 8px

//-- sprite icons
// auto generate classes for sprites
@include sprites($spritesheet-sprites);

//[class*="sprite-"]
// //replace styles in template
//display: inline-block
//vertical-align: middle

.svg-icon {
	display: inline-block;
	vertical-align: middle;
	stroke-width: 0;
	stroke: currentColor;
	fill: currentColor; }

.ntc-button {
	position: fixed;
	bottom: 0;
	right: 0;
	z-index: 999; }
