.m-nav {
	$this: &;
	margin-top: 46px;
	perspective: 1000px;
	perspective-origin: center center;
	@include min-screen($mid-tablet) {
		margin-top: 82px; }
	@include max-screen($small-tablet) {
		perspective: 200px; }

	&__item {
		border-top: 1px solid $color-border;
		transform-origin: 50% 50%;
		&:last-child {
			border-bottom: 1px solid $color-border; }
		&.has-child {
			#{$this}__link {
				position: relative;
				width: 100%;
				&:before, &:after {
					display: block;
					content: "";
					position: absolute;
					top: 50%;
					right: 10px;
					width: 30px;
					transform-origin: 50% 50%;
					height: 2px;
					margin-top: -1px;
					background: $color-white;
					transition: all .2s;
					@include min-screen($mid-tablet) {
						right: 50px; } }
				&:after {
					transform: scaleY(1) rotate(90deg); } } } }

	&__link {
		@extend h2;
		display: inline-block;
		padding: 26px 50px 20px 0;
		color: $color-white;
		transition: color .2s;
		cursor: pointer;
		user-select: none;
		@include min-screen($mid-tablet) {
			padding: 26px 90px 20px 0; }
		@include max-screen($smartphone-portrait) {
			font-size: 16px; }
		&.is-current, &:hover, &:focus {
			color: $color-purple;
			&:after, &:before {
				background: $color-purple !important; } }

		&.is-open {
			&:before {
				transform: rotate(180deg) !important; }
			&:after {
				transform: scaleY(0) rotate(180deg) !important; } } } }

.m-subnav {
	@include make-row();
	--bs-gutter-y: 18px;
	//padding-bottom: 20px
	max-width: 920px;
	overflow: hidden;
	height: 0;
	margin-top: 0 !important;
	@include max-screen($smartphone-landscape) {
		--bs-gutter-y: 10px; }
	&__item {
		@include make-col-ready();
		@include min-screen($smartphone-landscape) {
			@include make-col(6); }

		&:last-child {
			margin-bottom: 14px; }

		&.is-current {
			.m-subnav__link {
				color: $color-purple; } } }

	&__link {
		@extend h5;
		display: inline-block;
		color: $color-white;
		transition: color .2s;
		&:hover, &:focus {
			color: $color-purple; } } }
