// custom generator
$container-types: (default: 1400px);
$maws-types: (md: 780px);
$block-types: (default: 120px, md: 70px, sm: 50px);
$offset-types: (default: 120px, md: 70px, sm: 50px);

// override if need  usage: +container(maw, gutter)
// true - размеры, gutter-ы не влияют

@include create-containers($container-types, 20px);
@include create-maws($maws-types); // max-width containers
@include create-spaces($block-types); // section paddings
@include create-offsets($offset-types); // section margins

// bootsrap grid
$enable-caret:                false;
$enable-rounded:              false;
$enable-shadows:              false;
$enable-gradients:            false;
$enable-transitions:          false;
$enable-reduced-motion:       false;
$enable-smooth-scroll:        false;
$enable-grid-classes:         true;
$enable-button-pointers:      false;
$enable-rfs:                  false;
$enable-validation-icons:     false;
$enable-negative-margins:     false;
$enable-deprecation-messages: false;
$enable-important-utilities:  false;

$grid-breakpoints: (xs: 0, sp: 381px, sl: 481px, sm: 576px, md: 769px, lg: 992px, xl: 1200px);
$grid-columns: 12;
$grid-row-columns: 6;
$grid-gutter-width: 40px;

// .gy-${x} grid modifiers
$gutters: (1: 30px, 2: 50px, 3: 70px);

// bootstrap margins / paddings
$spacer: 10px;
$spacers: (0: 0, 1: $spacer / 4, 2: $spacer / 2, 3: $spacer, 4: $spacer * 1.5, 5: $spacer * 3);

// remodal
$remodal-sizes: (default: 800px, small: 500px);

// button effects
$primaryColor: $color-main;
$activeColor: $color-main--dark;
