//noinspection SassScssResolvedByNameOnly
@include font("circe-reg", Circe-Regular)
@include font("glock-medium", GlockGrotesque-Medium)

$ffamily-reg: "circe-reg";
$ffamily-bold: "glock-medium";


$headlinesMap: (
	h1: 54px,
	h2: 38px,
	h3: 30px,
	h4: 26px,
	h5: 20px,
);

@mixin responsive-fonts($size) {
	font-size: $size;

	@include max-screen($desktop) {
		font-size: floorFonts($size, 0.9);
	}
	@include max-screen($big-tablet) {
		font-size: floorFonts($size, 0.8);
	}

	@if ($size > 30px) {
		@include max-screen($common-tablet) {
			font-size: floorFonts($size, 0.7);
		}
		@include max-screen($smartphone-landscape) {
			font-size: floorFonts($size, 0.6);
		}
		@include max-screen($smartphone-portrait) {
			font-size: floorFonts($size, 0.5);
		}
	}
}

// герерация Hn, .hn-like классов
@each $headline, $px in $headlinesMap {
	#{$headline}, .#{$headline}-like {
		@include responsive-fonts($px);

		font-family: $ffamily-bold;
		font-weight: normal;
		line-height: 1.3;
	}
}

//****** Typography
h1, .h1-like {
	line-height: 1.11;
	@include max-screen($smartphone-portrait) {
		font-size: 25px;
	}
}
h2, .h2-like {
	line-height: 1.15;
}
h3, .h3-like {
	line-height: 1.46;
}
h4, .h4-like {
	font-family: $ffamily-reg;
	line-height: 1.3;
	// TODO: починить баг-фичу
	@include max-screen($smartphone-landscape) {
		font-size: 18px;
	}
	@include max-screen($smartphone-portrait) {
		font-size: 16px;
	}
}
h5, .h5-like {
	font-family: $ffamily-reg;
	line-height: 1.4;
}

p + p {
	margin-top: 22px
}

strong, b {
	//font-family: $ffamily-bold;
	//font-weight: normal;
	font-weight: 600;
}
