/*
		|--------------------------------------------------------------------------
		| Containers, maws, blocks
		|--------------------------------------------------------------------------
	* *///

@function floorFonts($baseSize, $factor) {
	@return floor($baseSize * $factor); }

@mixin create-containers($map, $gutters: "20px") {
	.container-proto {
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		padding-left: $gutters;
		padding-right: $gutters; }

	@each $container-width in map-keys($map) {
		@if ($container-width == default) {
			.container {
				@extend .container-proto;
				max-width: map_get($map, $container-width) + ($gutters * 2); } }
		@else {
			.container-#{$container-width} {
				@extend .container-proto;
				max-width: map_get($map, $container-width) + ($gutters * 2); } } } }

// maws
@mixin create-maws($map) {
	// max-widths containers (without padding)
	[class*="maw-"] {
		width: 100%; }
	@each $maw in map-keys($map) {
		.maw-#{$maw} {
			max-width: map_get($map, $maw); } } }

// blocks
@mixin responsive-bpoints($gap, $cssPropsArr) {
	@include max-screen($notebook-width) {
		@each $cssProp in $cssPropsArr {
			#{$cssProp}: $gap * 0.8; } }
	@include max-screen($big-tablet) {
		@each $cssProp in $cssPropsArr {
			#{$cssProp}: $gap * 0.7; } }
	@include max-screen($small-tablet) {
		@each $cssProp in $cssPropsArr {
			#{$cssProp}: $gap * 0.65; } }
	@include max-screen($smartphone-portrait) {
		@each $cssProp in $cssPropsArr {
			#{$cssProp}: $gap * 0.5; } } }

$generator_vertical: "v";
$generator_top: "top";
$generator_bottom: "bottom";

@mixin create-spaces($map) {
	$paddingName: "space";

	@each $name, $gap in $map {
		@if ($name == "default") {
			.#{$paddingName}-#{$generator_vertical} {
				padding-top: $gap;
				padding-bottom: $gap;
				@include responsive-bpoints($gap, ("padding-top", "padding-bottom")); }
			.#{$paddingName}-#{$generator_top} {
				padding-top: $gap;
				@include responsive-bpoints($gap, ("padding-top")); }
			.#{$paddingName}-#{$generator_bottom} {
				padding-bottom: $gap;
				@include responsive-bpoints($gap, ("padding-bottom")); } }
		@else {
			.#{$paddingName}-#{$generator_vertical}_#{$name} {
				padding-top: $gap;
				padding-bottom: $gap;
				@include responsive-bpoints($gap, ("padding-top", "padding-bottom")); }
			.#{$paddingName}-#{$generator_top}_#{$name} {
				padding-top: $gap;
				@include responsive-bpoints($gap, ("padding-top")); }
			.#{$paddingName}-#{$generator_bottom}_#{$name} {
				padding-bottom: $gap;
				@include responsive-bpoints($gap, ("padding-bottom")); } } } }

@mixin create-offsets($map) {
	$marginName: "offset";

	@each $name, $gap in $map {
		@if ($name == "default") {
			.#{$marginName}-#{$generator_vertical} {
				margin-top: $gap;
				margin-bottom: $gap;
				@include responsive-bpoints($gap, ("margin-top", "margin-bottom")); }
			.#{$marginName}-#{$generator_top} {
				margin-top: $gap;
				@include responsive-bpoints($gap, ("margin-top")); }
			.#{$marginName}-#{$generator_bottom} {
				margin-bottom: $gap;
				@include responsive-bpoints($gap, ("margin-bottom")); } }
		@else {
			.#{$marginName}-#{$generator_vertical}_#{$name} {
				margin-top: $gap;
				margin-bottom: $gap;
				@include responsive-bpoints($gap, ("margin-top", "margin-bottom")); }
			.#{$marginName}-#{$generator_top}_#{$name} {
				margin-top: $gap;
				@include responsive-bpoints($gap, ("margin-top")); }
			.#{$marginName}-#{$generator_bottom}_#{$name} {
				margin-bottom: $gap;
				@include responsive-bpoints($gap, ("margin-bottom")); } } } }


/*
		|--------------------------------------------------------------------------
		| Backgrounds, colors
		|--------------------------------------------------------------------------
	* *///

@mixin create-background-classes($map) {
	@each $item, $value in $map {
		.bg-#{$item} {
			background-color: $value; } } }

@mixin create-colors-classes($map) {
	@each $item, $value in $map {
		.clr-#{$item} {
			color: $value; } } }

/*
		|--------------------------------------------------------------------------
		| Helpers
		|--------------------------------------------------------------------------
	* *///

@function strip-unit($number) {
	@if type-of($number) == 'number' and not unitless($number) {
		@return $number / ($number * 0 + 1); }
	@return $number; }

@function getVWsize($px, $screen: 1920) {
	@return ($px / strip-unit($screen) *100vw); }
