.breadcrumbs {
	margin-top: 40px;
	//margin-bottom: 40px
	@include max-screen($desktop) {
		margin-top: 20px; }
	&__inner {}
	&__list {
		display: flex;
		flex-flow: row wrap;
		margin-left: -10px;
		font-size: 12px; }
	&__item {
		position: relative;
		padding-left: 10px;
		padding-right: 10px;
		&:nth-last-child(1n+2) {
			&:after {
				display: inline-block;
				content: "—";
				position: absolute;
				top: 0;
				right: -4px; } } }
	&__link {
		@extend .link;
		color: $color-text-gray;
		&.is-current {
			color: $color-black; } } }
