//hovers
@mixin animate-to-right($color: $color-main, $height: 2px) {
	display: inline-block;
	position: relative;
	&:after {
		display: block;
		content: " ";
		width: 0;
		height: $height;
		background: $color;
		position: absolute;
		left: 0;
		bottom: -2px;
		transition: width .5s; }
	&:hover {
		color: $color; }
	&:hover:after {
		width: 100%; } }

@mixin animate-from-center($color: $color-main, $height: 2px, $bottom-position: 0, $transition-time: 0.3s) {
	display: inline-block;
	position: relative;
	color: $color;
	&:before {
		content: "";
		position: absolute;
		width: 100%;
		height: $height;
		bottom: $bottom-position;
		left: 0;
		background-color: $color;
		visibility: hidden;
		transform: scaleX(0);
		transition: all $transition-time ease-in-out 0s; }
	&:hover:before, &:focus:before {
		visibility: visible;
		transform: scaleX(1); } }

// !css - animations
@mixin defaultAnimation {
	animation-duration: 0.5s;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.550, 0.085, 0.680, 0.530); }

@keyframes animateToRight {
	from {
		transform: translateX(0); }
	to {
		transform: translateX(4px); } }

@keyframes animateToUp {
	from {
		transform: translateY(0); }
	to {
		transform: translateY(-4px); } }

@keyframes animateToLeft {
	from {
		transform: translateX(0); }
	to {
		transform: translateX(-4px); } }

@keyframes animateToTopLeft {
	from {
		transform: translate(0, 0); }
	to {
		transform: translate(-2px, -2px); } }

@keyframes animateToBottomRight {
	from {
		transform: translate(0, 0); }
	to {
		transform: translate(2px, 2px); } }

