	.swiper-slide {
		.lazy-wrap {
			visibility: hidden; }
		&.swiper-slide:first-child,
		&.swiper-slide-prev,
		&.swiper-slide-active,
		&.swiper-slide-next,
		&.swiper-slide-loaded {
			.lazy-wrap {
				visibility: visible; } } } }
