.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	background: $color-black;
	z-index: 4;
	color: $color-white;
	//transition: background .3s cubic-bezier(.215,.61,.355,1)
	//will-change: transform
	&__inner {
		display: flex;
		align-items: center;
		height: var(--header_height); }
	&__rightbar {
		display: flex;
		align-items: center;
		margin-left: auto; }
	&__logo {
		max-width: 170px;
		margin-right: 10px;
		@include max-screen($smartphone-portrait) {
			padding-right: 20px; }
		@include max-screen($smartphone-wide) {
			max-width: 140px; }
		img {
			width: 100%; } }
	&__feedback {
		@include max-screen($ipad-landscape) {
			display: none; } }
	&__hamburger {
		padding-top: 8px;
		margin-left: 14px;
		@include min-screen(420px) {
			margin-left: 20px; }
		@include min-screen($smartphone-landscape) {
			margin-left: 30px; }
		@include min-screen($mid-tablet) {
			margin-left: 66px; } }

	&__buy {
		white-space: nowrap; } }
