.overflow-container {
	overflow: auto;
	-webkit-overflow-scrolling: touch; }

.video-16-9 {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%; } }

.trim-free {
	max-height: 0 !important;
	overflow: visible !important; }
