.p-404 {}

.notfound {
	@extend .space-v;
	display: flex;
	position: relative;
	min-height: calc(100vh - var(--header_height));
	@include max-screen($mid-tablet) {
		flex-direction: column;
		min-height: auto; }
	&__inner {
		position: relative;
		z-index: 1;
		display: flex;
		flex-direction: column; }
	&__numbers {
		font-family: $ffamily-bold;
		font-size: 150px;
		@include max-screen($smartphone-wide) {
			font-size: clamp(50px, 30vw, 150px); } }

	&__text {
		@extend h2;
		@extend .offset-v_md;
		@include max-screen($big-tablet) {
			margin-top: 0; } }

	&__back {
		max-width: 266px;
		width: 100%;
		margin-top: auto; }

	&__img-w {
		@include min-screen($mid-tablet) {
			@include centre(y);
			right: 140px; }
		@include max-screen(1480px) {
			right: 20px; }
		@include max-screen($notebook-width) {
			max-width: calc(100% - 460px); }
		@include max-screen($mid-tablet) {
			max-width: 100%;
			margin: 40px auto 0; } } }
