// Lazyload effects
// ---------------------------
//img.lazyload:not([src]) {
//	visibility: hidden;
//}


.loading,
.lazyload,
.lazyloaded,
.image {
	transition: 1s cubic-bezier(0.215, 0.61, 0.355, 1);
	opacity: 1;
}

// Loading
// ---------------------------
@keyframes scaleout {
	0% {
		transform: scale(0);
	}

	100% {
		transform: scale(1);
		opacity: 0;
	}
}

.lazy-wrap {
	position: relative;
	overflow: hidden;
	background-color: #f6f6f6;
	&--loading {
		img {
			opacity: 0;
			transform: translateY(8%);
		}
		&:before {
			content: "";
			position: absolute;
			z-index: 3;
			top: 50%;
			left: 50%;
			margin-top: -16px;
			margin-left: -16px;
			width: 32px;
			height: 32px;
			border-radius: 32px;
			animation: scaleout 1.2s infinite ease-in-out;
			// Tweak as needed
			//mix-blend-mode: soft-light;
			mix-blend-mode: darken;
			background-color: rgba(0, 0, 0, .9);
		}
	}

	img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

	}
}
