.p-reasons {}

.reasons-head {
	@extend .offset-bottom_md;
	&__inner {
		display: flex;
		align-items: center; }
	&__number {
		@extend h1;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		max-width: 240px;
		height: 92px;
		padding-top: 24px;
		background: $color-yellow;
		border-radius: 50%;
		@include max-screen($common-tablet) {
			padding-top: 20px; }
		@include max-screen($smartphone-landscape) {
			padding-top: 14px; } }

	&__title {
		margin-left: 40px;
		@include max-screen($smartphone-landscape) {
			margin-left: 30px; }
		@include max-screen($smartphone-portrait) {
			margin-left: 20px; } } }


.reasons-body {
	$this: &;
	&__inner {
		@extend .container;
		@extend .container--space_x; }
	&__block {
		& + & {
			@extend .offset-top_sm; } } }

.list-comments {
	&__item {
		&:nth-child(1n+2) {
			@extend .offset-top_sm; } } }

.list-reasons {
	&__item {
		&:nth-child(1n+2) {
			@extend .offset-top; } } }
